<template>
  <div>
    <section class="order">
      <div class="container container--wide">
        <Breadcrumbs :breadcrumbs="breadcrumbs" :title="title"></Breadcrumbs>
        <div class="container">
          <div class="order__inner">
            <form
              action="get"
              id="form-order"
              class="form order__form form--order"
            >
              <div class="order__form-wrapper">
                <div class="form__fields">
                  <div class="form__group">
                    <h3 class="order__title" v-html="contacts_title"></h3>
                    <div class="form__field">
                      <div class="form__field-wrapper">
                        <label
                          :for="form.name.id"
                          class="form__label"
                          v-html="form.name.label"
                        ></label>
                        <input
                          :name="form.name.id"
                          :id="form.name.id"
                          :type="form.name.type"
                          v-model="form.name.value"
                          class="form__input"
                          required="required"
                          :class="{
                            'form__input--error': form.name.error,
                            'form__input--filled': form.name.value.length > 0,
                          }"
                        />
                      </div>
                      <template v-if="form.name.error">
                        <span
                          class="form__error"
                          v-html="form.name.error"
                        ></span>
                      </template>
                    </div>
                    <div class="form__field">
                      <div class="form__field-wrapper">
                        <label
                          :for="form.phone.id"
                          class="form__label"
                          v-html="form.phone.label"
                        ></label>
                        <input
                          :name="form.phone.id"
                          :id="form.phone.id"
                          :type="form.phone.type"
                          v-model="form.phone.value"
                          class="form__input"
                          required="required"
                          :class="{
                            'form__input--error': form.phone.error,
                            'form__input--filled': form.phone.value.length > 0,
                          }"
                          v-mask="'+7 (###) ###-##-##'"
                        />
                      </div>
                      <template v-if="form.phone.error">
                        <span
                          class="form__error"
                          v-html="form.phone.error"
                        ></span>
                      </template>
                    </div>
                    <div class="form__field">
                      <div class="form__field-wrapper">
                        <label
                          :for="form.email.id"
                          class="form__label"
                          v-html="form.email.label"
                        ></label>
                        <input
                          :name="form.email.id"
                          :id="form.email.id"
                          :type="form.email.type"
                          v-model="form.email.value"
                          class="form__input"
                          :class="{
                            'form__input--error': form.email.error,
                            'form__input--filled': form.email.value.length > 0,
                          }"
                        />
                      </div>
                      <template v-if="form.email.error">
                        <span
                          class="form__error"
                          v-html="form.email.error"
                        ></span>
                      </template>
                    </div>
                  </div>
                  <div class="form__group">
                    <h3 class="order__title" v-html="delivery_title"></h3>
                    <div class="form__field form__field--radio">
                      <span v-html="delivery_method" class="form__label"></span>
                      <fieldset class="form__fieldset">
                        <div class="form__field-wrapper">
                          <input
                            name="delivery"
                            :id="form.delivery.bypost.id"
                            type="radio"
                            :value="form.delivery.bypost.label"
                            v-model="form.delivery.value"
                            class="form__radio"
                            checked
                          />
                          <label
                            :for="form.delivery.bypost.id"
                            v-html="form.delivery.bypost.label"
                          ></label>
                        </div>
                        <div class="form__field-wrapper">
                          <input
                            name="delivery"
                            :id="form.delivery.pickup.id"
                            type="radio"
                            :value="form.delivery.pickup.label"
                            v-model="form.delivery.value"
                            class="form__radio"
                          />
                          <label
                            :for="form.delivery.pickup.id"
                            v-html="form.delivery.pickup.label"
                          ></label>
                        </div>
                      </fieldset>
                    </div>
                    <div class="form__field form__field--textarea">
                      <div class="form__field-wrapper">
                        <label
                          :for="form.address.id"
                          class="form__label"
                          v-html="form.address.label"
                        ></label>
                        <textarea
                          :name="form.address.id"
                          :id="form.address.id"
                          v-model="form.address.value"
                          :maxlength="form.address.maxlength"
                          cols="34"
                          rows="10"
                          class="form__textarea"
                          :class="{
                            'form__textarea--error':
                              form.address.error ||
                              form.address.value.length ===
                                form.address.maxlength,
                            'form__textarea--filled':
                              form.address.value.length > 0,
                          }"
                        ></textarea>
                      </div>
                      <template v-if="form.address.error">
                        <span
                          class="form__error"
                          v-html="form.address.error"
                        ></span>
                      </template>
                    </div>
                    <div class="form__field form__field--radio">
                      <span v-html="payment_method" class="form__label"></span>
                      <fieldset class="form__fieldset">
                        <div class="form__field-wrapper">
                          <input
                            name="payment"
                            :id="form.payment.card.id"
                            type="radio"
                            :value="form.payment.card.label"
                            v-model="form.payment.value"
                            class="form__radio"
                            checked
                          />
                          <label
                            :for="form.payment.card.id"
                            v-html="form.payment.card.label"
                          ></label>
                        </div>
                        <div class="form__field-wrapper">
                          <input
                            name="payment"
                            :id="form.payment.cash.id"
                            type="radio"
                            :value="form.payment.cash.label"
                            v-model="form.payment.value"
                            class="form__radio"
                          />
                          <label
                            :for="form.payment.cash.id"
                            v-html="form.payment.cash.label"
                          ></label>
                        </div>
                      </fieldset>
                    </div>
                  </div>
                </div>
                <div class="order__total">
                  <div class="order__total-wrapper">
                    <h5 class="order__total-title" v-html="total_title"></h5>
                  <div
                    class="order__product"
                    v-for="(product, p) in products"
                    :key="p"
                  >
                    <div class="order__product-info">
                      <h5
                        class="order__product-name"
                        v-html="product.name"
                      ></h5>
                    </div>
                  </div>
                  <hr>
                  <div class="order__total-summa">
                    <span v-html="total"></span> <span v-html="currency"></span><span v-html="summa"></span>
                  </div>
                  </div>
                </div>
              </div>
              <button v-if="form.payment.value === '  '"
                type="submit"
                name="submit"
                class="button button--black button--big button--submit"
                @click.prevent="sendFrom()"
                v-html="button_pay"
              ></button>
              <button v-else
                type="submit"
                name="submit"
                class="button button--black button--big button--submit"
                @click.prevent="sendFrom()"
                v-html="button_end"
              ></button>
            </form>
          </div>
        </div>
      </div>
    </section>
    <transition name="fade">
      <OrderSuccess />
    </transition>
  </div>
</template>

<script>
import Breadcrumbs from "../common/Breadcrumbs.vue";
import axios from "axios";
import OrderSuccess from "../modals/OrderSuccess.vue";

export default {
  name: "Order",
  components: {
    Breadcrumbs,
    OrderSuccess,
  },
  data() {
    return {
      breadcrumbs: [
        {
          text: "",
          link: "/home.html",
        },
        {
          text: "",
          link: "/cart.html",
        },
        {
          text: " ",
          link: "",
        },
      ],
      title: " ",
      contacts_title: " ",
      delivery_title: "  ",
      delivery_method: " ",
      payment_method: " ",
      total_title: " ",
      total: ":",
      button_pay: "  ",
      button_end: " ",
      currency: "$",
      products: [
        {
          name: "    ,   ",
          price: 1200,
        },
        {
          name: "    ",
          price: 200,
        },
        {
          name: "    ,   ",
          price: 45400,
        },
      ],
      form: {
        name: {
          value: "",
          label: ", *",
          type: "text",
          id: "order-name",
          error: "",
        },
        phone: {
          value: "",
          label: "*",
          type: "tel",
          id: "order-phone",
          error: "",
        },
        email: {
          value: "",
          label: "E-mail",
          type: "email",
          id: "order-email",
          error: "",
        },
        delivery: {
          value: " ",
          bypost: {
            label: " ",
            id: "order-delivery_bypost",
          },
          pickup: {
            label: "",
            id: "order-delivery_pickup",
          },
        },
        address: {
          value: "",
          label: "",
          type: "textarea",
          id: "order-address",
          maxlength: 1000,
          error: "",
        },
        payment: {
          value: "  ",
          card: {
            label: "  ",
            id: "order-payment_card",
          },
          cash: {
            label: " ",
            id: "order-payment_cash",
          },
        },
      },
    };
  },
  props: {},
  computed: {
    summa() {
      let sum = 0;
      for (let key in this.products) {
        sum += parseFloat(this.products[key].price);
      }
      return sum;
    },
  },
  mounted() {},
  methods: {
    async sendFrom() {
      if (!this.$validate(this.form)) {
        return;
      }

      const data = new FormData();
      for (let key in this.form) {
        if (typeof this.form[key].value !== "undefined") {
          data.append(key, this.form[key].value);
        }
      }
      const token = await this.$root.getRecaptchaToken("discount");
      data.append("recaptcha_response", token);
      data.append("method", "request");

      const response = await axios.post("/api/", data).catch((error) => {
        console.log(error);
      });

      // if (response.data.success) {
      this.$store.commit("OrderSuccessOpen");
      // }
    },
  },
};
</script>
